import React from 'react';
import { Card, Row, Col, Select, Button, notification, Tag, Input, Checkbox, DatePicker } from 'antd';
import { PlusOutlined, MinusCircleOutlined } from '@ant-design/icons';
import dayjs from 'dayjs';

const { Option } = Select;
const { TextArea } = Input;

const LostClientCard = ({ title, month, setMonth, clients, setClients, includeClients, setIncludeClients }) => {

  //     
  const cardText = () => {
    let clientText = clients.map((client, index) => (
      `\n${index + 1}. : ${client.username}${client.service ? `, : ${client.service}` : ''}${client.dealDate ? `,  : ${dayjs(client.dealDate).format('DD.MM.YYYY')}` : ''}${client.comment ? `,   . : ${client.comment}` : ''}`
    )).join('');

    let clientCount = clients.length;
    let introText = clientCount === 1 
      ? '     .   :' 
      : '     .   :';

    let possibleMistakeText = clientCount === 1 
      ? ',   -  ?' 
      : ',   -  ?';

    let message = ` !\n\n          ()   ${month},  ,     .  ,   .     .\n, ,     ?`;

    if (includeClients) {
      message += `\n\n${introText} ${clientText}\n\n${possibleMistakeText}\n\n, ,    . , ,    ?`;
    }

    return message;
  };

  //      
  const copyToClipboard = () => {
    const tempElement = document.createElement('textarea');
    tempElement.value = cardText();
    document.body.appendChild(tempElement);
    tempElement.select();
    document.execCommand('copy');
    document.body.removeChild(tempElement);
    
    notification.success({
      message: '!',
      description: '   .',
    });
  };

  //    
  const addClientBlock = () => {
    setClients([...clients, { username: '', service: '', dealDate: null, comment: '' }]);
  };

  //   
  const removeClientBlock = (index) => {
    const updatedClients = clients.filter((_, i) => i !== index);
    setClients(updatedClients);
  };

  //    
  const handleClientChange = (index, field, value) => {
    const updatedClients = [...clients];
    updatedClients[index][field] = value;
    setClients(updatedClients);
  };

  return (
    <Row gutter={16}>
      <Col span={24}>
        <Card 
          title={title}
          bordered={false}
          extra={<Button onClick={copyToClipboard}></Button>}
          actions={[
            <Tag color="magenta">#</Tag>
          ]}
        >
          <p> !</p>
          <p>
                      ()  {' '}
            <Select 
              value={month} 
              onChange={(value) => setMonth(value)} 
              style={{ width: '120px', marginLeft: '10px' }}
            >
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
            ,  ,     .  ,   .     .
          </p>
          <p>, ,     ?</p>

          <Checkbox
            checked={includeClients}
            onChange={(e) => setIncludeClients(e.target.checked)}
          >
               
          </Checkbox>

          {includeClients && (
            <>
              <p><strong>{clients.length === 1 ? '     .   :' : '     .   :'}</strong></p>
              
              {clients.map((client, index) => (
                <div key={index} style={{ marginBottom: '10px' }}>
                  <Input 
                    placeholder=" " 
                    value={client.username} 
                    onChange={(e) => handleClientChange(index, 'username', e.target.value)}
                    style={{ marginBottom: '5px' }}
                  />
                  <Input 
                    placeholder=" ()" 
                    value={client.service} 
                    onChange={(e) => handleClientChange(index, 'service', e.target.value)}
                    style={{ marginBottom: '5px' }}
                  />
                  <DatePicker
                    placeholder=" "
                    style={{ width: '100%', marginBottom: '5px' }}
                    value={client.dealDate ? dayjs(client.dealDate) : null}
                    onChange={(date) => handleClientChange(index, 'dealDate', date)}
                  />
                  <TextArea
                    placeholder="   .  ()"
                    value={client.comment}
                    onChange={(e) => handleClientChange(index, 'comment', e.target.value)}
                    rows={3}
                  />
                  <Button 
                    type="link" 
                    icon={<MinusCircleOutlined />} 
                    onClick={() => removeClientBlock(index)}
                    style={{ color: 'red', marginTop: '10px' }}
                  >
                     
                  </Button>
                </div>
              ))}

              <Button 
                type="dashed" 
                onClick={addClientBlock} 
                style={{ width: '100%', marginTop: '10px' }}
              >
                <PlusOutlined />  
              </Button>

              <p style={{ marginTop: '20px' }}>
                {clients.length === 1 ? ',   -  ?' : ',   -  ?'}
              </p>
              <p>
                , ,    . , ,    ?
              </p>
            </>
          )}
        </Card>
      </Col>
    </Row>
  );
};

export default LostClientCard;
