import React, { useState } from 'react';
import { Row, Col, Input } from 'antd';
import LostClientCard from './Cards/LostClientCard'; //     
import UnapprovedServiceCard from './Cards/UnapprovedServiceCard'; //     
import Notes from './Cards/Notes'; //    

const { Search } = Input;

const KatyaComponent = () => {
  const [month, setMonth] = useState('');
  const [includeClients, setIncludeClients] = useState(false);

  //       
  const [clients, setClients] = useState([
    { username: '', service: '', dealDate: null, comment: '' }
  ]);

  //        
  const [service, setService] = useState({
    clientName: '',
    serviceName: '',
    clientPrice: '',
  });

  //    
  const [searchQuery, setSearchQuery] = useState('');

  //      LostClientCard
  const handleClientChange = (index, field, value) => {
    const updatedClients = clients.map((client, i) =>
      i === index ? { ...client, [field]: value } : client
    );
    setClients(updatedClients);
  };

  //    LostClientCard   
  const copyLostClientCard = () => {
    let clientText = clients.map((client, index) => (
      `: ${client.username}, : ${client.service},  : ${client.dealDate}, : ${client.comment}`
    )).join('\n');
    const text = `
             ${month}.  :
      ${clientText}
    `;
    navigator.clipboard.writeText(text);
    alert('  ');
  };

  //      UnapprovedServiceCard
  const handleServiceChange = (field, value) => {
    setService((prevService) => ({
      ...prevService,
      [field]: value
    }));
  };

  //    
  const filteredCards = [
    {
      title: '',
      component: (
        <LostClientCard
          title=""
          month={month}
          setMonth={setMonth}
          clients={clients}
          setClients={setClients}
          includeClients={includeClients}
          setIncludeClients={setIncludeClients}
          copyToClipboard={copyLostClientCard} //   
        />
      )
    },
    {
      title: ' ',
      component: (
        <UnapprovedServiceCard
          service={service}
          handleServiceChange={handleServiceChange}
        />
      )
    }
  ].filter(card => card.title.toLowerCase().includes(searchQuery.toLowerCase()));

  return (
    <>
      {/*   */}
      <Row style={{ marginBottom: '16px' }}>
        <Col span={16}>
          <Search
            placeholder="   "
            onChange={(e) => setSearchQuery(e.target.value)}
            enterButton
          />
        </Col>
      </Row>

      <Row gutter={16}>
        <Col span={16}>
          {/*   */}
          {filteredCards.map((card, index) => (
            <div key={index}>
              {card.component}
            </div>
          ))}
        </Col>

        {/*   */}
        <Col span={8}>
          <Notes />
        </Col>
      </Row>
    </>
  );
};

export default KatyaComponent;
