import React, { useState } from 'react';
import { Input, Button, Form, Typography, notification } from 'antd';

const { TextArea } = Input;
const { Title } = Typography;

const ExpertDigestForm = () => {
  const [digestNumber, setDigestNumber] = useState('');
  const [newExperts, setNewExperts] = useState('');
  const [featuredExperts, setFeaturedExperts] = useState('');
  const [bannedExperts, setBannedExperts] = useState('');
  const [removedExperts, setRemovedExperts] = useState('');
  const [finalMessage, setFinalMessage] = useState('');

  const generateMessage = () => {
    const defaultNewExpertsMessage = ",          ,     )";
    const defaultFeaturedExpertsMessage = " ,          ";
    const defaultRemovedExpertsMessage = "       ";

    let message = `  #${digestNumber} \n\n` +
      "  ,   ,         .  \n\n" +
      `${newExperts.trim() ? `**,        :**\n${newExperts}` : defaultNewExpertsMessage}\n\n` +
      `${featuredExperts.trim() ? `** ,     **\n${featuredExperts}` : defaultFeaturedExpertsMessage}\n\n`;

    if (bannedExperts.trim()) {
      message += `** ,       :**\n${bannedExperts}\n\n`;
    }

    message += `${removedExperts.trim() ? `** , ,    :**\n${removedExperts}` : defaultRemovedExpertsMessage}\n\n` +
      "**    !**";

    setFinalMessage(message);
    copyToClipboard(message);
  };

  const copyToClipboard = (text) => {
    const tempElement = document.createElement('textarea');
    tempElement.value = text;
    document.body.appendChild(tempElement);
    tempElement.select();
    document.execCommand('copy');
    document.body.removeChild(tempElement);
    
    notification.success({
      message: '!',
      description: '     .',
      placement: 'top',
    });
  };

  return (
    <div style={{ padding: '20px' }}>
      <Title level={2}> </Title>

      <Form layout="vertical">
        <Form.Item 
          label=" :"
          required
          validateStatus={digestNumber ? 'success' : 'error'}
          help={!digestNumber && ",   "}
        >
          <Input 
            value={digestNumber} 
            onChange={(e) => setDigestNumber(e.target.value)} 
            placeholder="  "
          />
        </Form.Item>

        <Form.Item label="   :">
          <TextArea 
            value={newExperts} 
            onChange={(e) => setNewExperts(e.target.value)} 
            rows={5} 
            placeholder="      "
          />
        </Form.Item>

        <Form.Item label="  :">
          <TextArea 
            value={featuredExperts} 
            onChange={(e) => setFeaturedExperts(e.target.value)} 
            rows={5} 
            placeholder="    "
          />
        </Form.Item>

        <Form.Item label=" ,    :">
          <TextArea 
            value={bannedExperts} 
            onChange={(e) => setBannedExperts(e.target.value)} 
            rows={5} 
            placeholder="      "
          />
        </Form.Item>

        <Form.Item label="  :">
          <TextArea 
            value={removedExperts} 
            onChange={(e) => setRemovedExperts(e.target.value)} 
            rows={5} 
            placeholder="      "
          />
        </Form.Item>

        <Form.Item>
          <Button type="primary" onClick={generateMessage} disabled={!digestNumber}>
               
          </Button>
        </Form.Item>
      </Form>

      {finalMessage && (
        <div style={{ marginTop: '20px' }}>
          <Title level={3}> :</Title>
          <pre style={{
            backgroundColor: '#f9f9f9',
            padding: '15px',
            borderRadius: '8px',
            border: '1px solid #ddd',
            whiteSpace: 'pre-wrap',
            wordBreak: 'break-word'
          }}>
            {finalMessage}
          </pre>
        </div>
      )}
    </div>
  );
};

export default ExpertDigestForm;
