import React, { useEffect, useState } from 'react';
import { Modal, Button } from 'antd';
import dayjs from 'dayjs';
import { fetchSheetData } from './api'; // ,        Google Sheets API

const spreadsheetId = '1ACXyzSPRJpUynAlVman0FfDt2hieyi-7U86aLqxsR10'; //  ID 

const EventModalComponent = ({ modalData, setModalData, selectedCalendars }) => {
  const [partnerInfo, setPartnerInfo] = useState(null);
  const [searchInfo, setSearchInfo] = useState(null);
  const [serviceInfo, setServiceInfo] = useState(null); //  state   

  //  ,   
  const calendarData = selectedCalendars.find(cal => cal.id === modalData?.organizer.email);

  //       
  const extractUsername = (summary) => {
    const match = summary.match(/@[\w_]+/); //  ,    @    ,   _
    return match ? match[0] : null;
  };

  //      ":"  ":"
  const extractServiceInfo = (text) => {
    const serviceMatch = text.match(/(?:|):\s*(.*)/i); //  ":"  ":"    
    return serviceMatch ? serviceMatch[1] : '   ';
  };

  //         
  const fetchInfoFromSheets = async () => {
    try {
      const ranges = [
        { sheet: '', range: 'A:I' },  //  A-I
        { sheet: '', range: 'A:C' },    //  A-C
      ];

      //    
      const sheetData = await fetchSheetData(spreadsheetId, ranges);

      //    
      const username = extractUsername(modalData?.summary);
      console.log(` : ${username}`);

      if (!username) {
        console.log('    .');
        return;
      }

      //   
      console.log('   :', sheetData);

      //     
      const partnersSheet = sheetData.find(sheet => sheet.sheet === '');
      if (partnersSheet) {
        console.log('   :', partnersSheet.values);
      }

      if (partnersSheet && username) {
        console.log(` : ${username}   I`);
        //  ,     I    
        const matchingRow = partnersSheet.values.find(row => row[8]?.trim() === username);
        console.log('   :', matchingRow);
        if (matchingRow) {
          const serviceDataRaw = matchingRow[6] || '   '; //  G
          const serviceData = extractServiceInfo(serviceDataRaw); //    ":"
          setServiceInfo(serviceData); //    
          console.log(':', serviceData);
        } else {
          console.log('     .');
        }
      }

      //     
      const searchSheet = sheetData.find(sheet => sheet.sheet === '');
      if (searchSheet) {
        console.log('   :', searchSheet.values);
      }

      if (searchSheet && username) {
        console.log(` : ${username}   C`);
        const matchingRow = searchSheet.values.find(row => row[2]?.trim() === username);
        console.log('   :', matchingRow);
        if (matchingRow) {
          setSearchInfo(matchingRow[6]);
          console.log(' :', matchingRow[6]);
        } else {
          console.log('     .');
        }
      }
    } catch (error) {
      console.error('     Google Sheets:', error);
    }
  };

  //    Google Sheets,   modalData
  useEffect(() => {
    if (modalData) {
      fetchInfoFromSheets();
    }
  }, [modalData]);

  return (
    <Modal
      title={modalData?.summary || ' '}
      open={!!modalData}  //  open  visible
      onCancel={() => setModalData(null)}
      footer={null}
    >
      {modalData && (
        <>
          <p><strong>:</strong> {calendarData?.summary || ' '}</p>
          <p><strong>:</strong> {modalData.start.dateTime ? dayjs(modalData.start.dateTime).format('DD MMMM YYYY, HH:mm:ss') : ' '}</p>
          <p><strong>:</strong> {modalData.end.dateTime ? dayjs(modalData.end.dateTime).format('DD MMMM YYYY, HH:mm:ss') : ' '}</p>
          {modalData.description && (
            <p><strong>:</strong> <span dangerouslySetInnerHTML={{ __html: modalData.description }} /></p>
          )}

          {/*    ,    */}
          {serviceInfo && (
            <p><strong>:</strong> {serviceInfo}</p>
          )}

          {/*      */}
          {searchInfo && (
            <p><strong> :</strong> {searchInfo}</p>
          )}

          <Button type="link" href={modalData.htmlLink} target="_blank">  Google </Button>
        </>
      )}
    </Modal>
  );
};

export default EventModalComponent;
