import React, { useState } from 'react';
import { Layout, Menu } from 'antd';
import { UserOutlined, FileTextOutlined, TeamOutlined } from '@ant-design/icons';
import ExpertDigestForm from './components/Digest/ExpertDigestForm';
import KatyaComponent from './components/Katya/KatyaComponent';
import VikaComponent from './components/VikaComponent';
import CalendarEvent from './components/Calendar/CalendarEvent';

const { Sider, Content } = Layout;

const App = () => {
  const [selectedMenu, setSelectedMenu] = useState('1');

  const renderContent = () => {
    switch (selectedMenu) {
      case '1':
        return <ExpertDigestForm />;
      case '2':
        return <KatyaComponent />;
      case '3':
        return <VikaComponent />;
      case '4':
        return <CalendarEvent />;
      default:
        return null;
    }
  };

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <SiderMenu selectedMenu={selectedMenu} setSelectedMenu={setSelectedMenu} />
      <Layout>
        <Content style={{ margin: '24px 16px', padding: 24, background: '#fff', minHeight: 360 }}>
          {renderContent()}
        </Content>
      </Layout>
    </Layout>
  );
};

const SiderMenu = ({ selectedMenu, setSelectedMenu }) => {
  const handleMenuClick = (e) => {
    setSelectedMenu(e.key);
  };

  return (
    <Sider collapsible>
      <Menu theme="dark" mode="inline" selectedKeys={[selectedMenu]} onClick={handleMenuClick}>
        <Menu.Item key="1" icon={<FileTextOutlined />}>
          
        </Menu.Item>
        <Menu.Item key="2" icon={<UserOutlined />}>
          
        </Menu.Item>
        <Menu.Item key="3" icon={<TeamOutlined />}>
          
        </Menu.Item>
        <Menu.Item key="4" icon={<UserOutlined />}>
          
        </Menu.Item>
      </Menu>
    </Sider>
  );
};

export default App;
