import React from 'react';
import { Card, Input, Button, notification } from 'antd';

const UnapprovedServiceCard = ({
  service = {},  //  service   
  handleServiceChange,
}) => {
  //        
  const copyToClipboard = () => {
    //    ,   
    const text = `
       !

      ,      ${service.clientName || ' '}      ${service.serviceName || ' '},  ${service.clientPrice || ''}.  ,              ,    . , ,     ,     . .

      , ,  :
       -
      ,    -  
         - 
       - 
        - 

        %   ( =  - ) - 
      ,  ,    .
         .    .
    `;

    navigator.clipboard.writeText(text);
    
    notification.success({
      message: ' !',
      description: '     .',
      placement: 'topRight',
    });
  };

  return (
    <Card
      title=" "
      bordered={false}
      extra={<Button onClick={copyToClipboard}></Button>}
    >
      <p> !</p>
      <p>
        ,     {' '}
        <Input
          placeholder=" "
          value={service.clientName || ''}  //    
          onChange={(e) => handleServiceChange('clientName', e.target.value)}  //   
          style={{ width: '300px', marginRight: '10px' }}
        /> 
            {' '}
        <Input
          placeholder=" "
          value={service.serviceName || ''}  //    
          onChange={(e) => handleServiceChange('serviceName', e.target.value)}  //   
          style={{ width: '300px', marginRight: '10px' }}
        />
        , {' '}
        <Input
          placeholder="  "
          value={service.clientPrice || ''}  //     
          onChange={(e) => handleServiceChange('clientPrice', e.target.value)}  //    
          style={{ width: '120px' }}
        /> .
      </p>
      <p>
         ,              ,    . , ,     ,     . .
      </p>

      <p style={{ marginTop: '20px' }}>
        , ,  :
      </p>
      <ul>
        <li></li>
        <li>,   </li>
        <li>  </li>
        <li></li>
        <li> </li>
        <li>  %   ( =  - )</li>
      </ul>

      <p>
        ,  ,    .
      </p>
      <p>
            .    .
      </p>
    </Card>
  );
};

export default UnapprovedServiceCard;
